import "./style.css";

const Buttons = (props) => (
    props.tasks.length === 0 ?
        null
        :
        <div className="header__buttons">
            <button className={`buttons`}> {props.hideCheckTasks ? "Poka" : "Ukryj"} ukoczone</button>
            <button disabled={props.tasks.every(({ check }) => check)} className={`buttons`}> Ukocz wszystkie</button>
        </div>
)

export default Buttons;

